import React, { useEffect, useState } from "react";
import {
  Box,
  Typography,
  Button,
  TextField,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  IconButton,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  MenuItem,
  Select,
  InputLabel,
  FormControl,
} from "@mui/material";
import { Edit, Delete } from "@mui/icons-material";
import api from "../utils/api";

function CityPage() {
  const [cities, setCities] = useState([]);
  const [countries, setCountries] = useState([]);
  const [states, setStates] = useState([]);
  const [open, setOpen] = useState(false);
  const [editCity, setEditCity] = useState(null);
  const [form, setForm] = useState({ name: "", country: "", state: "" });

  const fetchCities = async () => {
    const res = await api.get("/api/cities");
    setCities(res.data);
  };
  const fetchCountries = async () => {
    const res = await api.get("/api/countries");
    setCountries(res.data);
  };
  const fetchStates = async (countryId) => {
    if (!countryId) return setStates([]);
    const res = await api.get(`/api/states/country/${countryId}`);
    setStates(res.data);
  };

  useEffect(() => {
    fetchCities();
    fetchCountries();
  }, []);

  useEffect(() => {
    if (form.country) fetchStates(form.country);
    else setStates([]);
  }, [form.country]);

  const handleOpen = (city = null) => {
    setEditCity(city);
    setForm(
      city
        ? {
            name: city.name,
            country: city.country?._id || "",
            state: city.state?._id || "",
          }
        : { name: "", country: "", state: "" }
    );
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
    setEditCity(null);
    setForm({ name: "", country: "", state: "" });
  };

  const handleSubmit = async () => {
    if (editCity) {
      await api.put(`/api/cities/${editCity._id}`, form);
    } else {
      await api.post("/api/cities", form);
    }
    fetchCities();
    handleClose();
  };

  const handleDelete = async (id) => {
    await api.delete(`/api/cities/${id}`);
    fetchCities();
  };

  return (
    <Box p={3}>
      <Typography variant="h6" mb={2}>
        Cities
      </Typography>
      <Button
        variant="contained"
        color="primary"
        onClick={() => handleOpen()}
        sx={{ mb: 2 }}
      >
        Add City
      </Button>
      <TableContainer component={Paper}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell>Name</TableCell>
              <TableCell>Country</TableCell>
              <TableCell>State</TableCell>
              <TableCell>Actions</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {cities.map((city) => (
              <TableRow key={city._id}>
                <TableCell>{city.name}</TableCell>
                <TableCell>{city.country?.name}</TableCell>
                <TableCell>{city.state?.name}</TableCell>
                <TableCell>
                  <IconButton onClick={() => handleOpen(city)}>
                    <Edit />
                  </IconButton>
                  <IconButton onClick={() => handleDelete(city._id)}>
                    <Delete />
                  </IconButton>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
      <Dialog open={open} onClose={handleClose}>
        <DialogTitle>{editCity ? "Edit City" : "Add City"}</DialogTitle>
        <DialogContent>
          <TextField
            label="Name"
            value={form.name}
            onChange={(e) => setForm({ ...form, name: e.target.value })}
            fullWidth
            margin="normal"
          />
          <FormControl fullWidth margin="normal">
            <InputLabel>Country</InputLabel>
            <Select
              value={form.country}
              label="Country"
              onChange={(e) =>
                setForm({ ...form, country: e.target.value, state: "" })
              }
            >
              {countries.map((country) => (
                <MenuItem key={country._id} value={country._id}>
                  {country.name}
                </MenuItem>
              ))}
            </Select>
          </FormControl>
          <FormControl fullWidth margin="normal">
            <InputLabel>State</InputLabel>
            <Select
              value={form.state}
              label="State"
              onChange={(e) => setForm({ ...form, state: e.target.value })}
              disabled={!form.country}
            >
              {states.map((state) => (
                <MenuItem key={state._id} value={state._id}>
                  {state.name}
                </MenuItem>
              ))}
            </Select>
          </FormControl>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose}>Cancel</Button>
          <Button onClick={handleSubmit} variant="contained">
            {editCity ? "Update" : "Add"}
          </Button>
        </DialogActions>
      </Dialog>
    </Box>
  );
}

export default CityPage;
