import React, { useEffect, useState } from "react";
import {
  Box,
  Typography,
  Button,
  TextField,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  IconButton,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
} from "@mui/material";
import { Edit, Delete } from "@mui/icons-material";
import api from "../utils/api";

function CountryPage() {
  const [countries, setCountries] = useState([]);
  const [open, setOpen] = useState(false);
  const [editCountry, setEditCountry] = useState(null);
  const [form, setForm] = useState({ name: "", code: "" });

  const fetchCountries = async () => {
    const res = await api.get("/api/countries");
    setCountries(res.data);
  };

  useEffect(() => {
    fetchCountries();
  }, []);

  const handleOpen = (country = null) => {
    setEditCountry(country);
    setForm(
      country
        ? { name: country.name, code: country.code }
        : { name: "", code: "" }
    );
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
    setEditCountry(null);
    setForm({ name: "", code: "" });
  };

  const handleSubmit = async () => {
    if (editCountry) {
      await api.put(
        `http://localhost:5000/api/countries/${editCountry._id}`,
        form
      );
    } else {
      await api.post("http://localhost:5000/api/countries", form);
    }
    fetchCountries();
    handleClose();
  };

  const handleDelete = async (id) => {
    await api.delete(`http://localhost:5000/api/countries/${id}`);
    fetchCountries();
  };

  return (
    <Box p={3}>
      <Typography variant="h6" mb={2}>
        Countries
      </Typography>
      <Button
        variant="contained"
        color="primary"
        onClick={() => handleOpen()}
        sx={{ mb: 2 }}
      >
        Add Country
      </Button>
      <TableContainer component={Paper}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell>Name</TableCell>
              <TableCell>Code</TableCell>
              <TableCell>Actions</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {countries.map((country) => (
              <TableRow key={country._id}>
                <TableCell>{country.name}</TableCell>
                <TableCell>{country.code}</TableCell>
                <TableCell>
                  <IconButton onClick={() => handleOpen(country)}>
                    <Edit />
                  </IconButton>
                  <IconButton onClick={() => handleDelete(country._id)}>
                    <Delete />
                  </IconButton>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
      <Dialog open={open} onClose={handleClose}>
        <DialogTitle>
          {editCountry ? "Edit Country" : "Add Country"}
        </DialogTitle>
        <DialogContent>
          <TextField
            label="Name"
            value={form.name}
            onChange={(e) => setForm({ ...form, name: e.target.value })}
            fullWidth
            margin="normal"
          />
          <TextField
            label="Code"
            value={form.code}
            onChange={(e) => setForm({ ...form, code: e.target.value })}
            fullWidth
            margin="normal"
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose}>Cancel</Button>
          <Button onClick={handleSubmit} variant="contained">
            {editCountry ? "Update" : "Add"}
          </Button>
        </DialogActions>
      </Dialog>
    </Box>
  );
}

export default CountryPage;
