import React, { useEffect, useState } from "react";
import {
  Box,
  Typography,
  Button,
  TextField,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  IconButton,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  MenuItem,
  Select,
  InputLabel,
  FormControl,
} from "@mui/material";
import { Edit, Delete } from "@mui/icons-material";
import api from "../utils/api";

function StatePage() {
  const [states, setStates] = useState([]);
  const [countries, setCountries] = useState([]);
  const [open, setOpen] = useState(false);
  const [editState, setEditState] = useState(null);
  const [form, setForm] = useState({ name: "", code: "", country: "" });

  const fetchStates = async () => {
    const res = await api.get("/api/states");
    setStates(res.data);
  };
  const fetchCountries = async () => {
    const res = await api.get("/api/countries");
    setCountries(res.data);
  };

  useEffect(() => {
    fetchStates();
    fetchCountries();
  }, []);

  const handleOpen = (state = null) => {
    setEditState(state);
    setForm(
      state
        ? {
            name: state.name,
            code: state.code,
            country: state.country?._id || "",
          }
        : { name: "", code: "", country: "" }
    );
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
    setEditState(null);
    setForm({ name: "", code: "", country: "" });
  };

  const handleSubmit = async () => {
    if (editState) {
      await api.put(`/api/states/${editState._id}`, form);
    } else {
      await api.post("/api/states", form);
    }
    fetchStates();
    handleClose();
  };

  const handleDelete = async (id) => {
    await api.delete(`/api/states/${id}`);
    fetchStates();
  };

  return (
    <Box p={3}>
      <Typography variant="h6" mb={2}>
        States
      </Typography>
      <Button
        variant="contained"
        color="primary"
        onClick={() => handleOpen()}
        sx={{ mb: 2 }}
      >
        Add State
      </Button>
      <TableContainer component={Paper}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell>Name</TableCell>
              <TableCell>Code</TableCell>
              <TableCell>Country</TableCell>
              <TableCell>Actions</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {states.map((state) => (
              <TableRow key={state._id}>
                <TableCell>{state.name}</TableCell>
                <TableCell>{state.code}</TableCell>
                <TableCell>{state.country?.name}</TableCell>
                <TableCell>
                  <IconButton onClick={() => handleOpen(state)}>
                    <Edit />
                  </IconButton>
                  <IconButton onClick={() => handleDelete(state._id)}>
                    <Delete />
                  </IconButton>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
      <Dialog open={open} onClose={handleClose}>
        <DialogTitle>{editState ? "Edit State" : "Add State"}</DialogTitle>
        <DialogContent>
          <TextField
            label="Name"
            value={form.name}
            onChange={(e) => setForm({ ...form, name: e.target.value })}
            fullWidth
            margin="normal"
          />
          <TextField
            label="Code"
            value={form.code}
            onChange={(e) => setForm({ ...form, code: e.target.value })}
            fullWidth
            margin="normal"
          />
          <FormControl fullWidth margin="normal">
            <InputLabel>Country</InputLabel>
            <Select
              value={form.country}
              label="Country"
              onChange={(e) => setForm({ ...form, country: e.target.value })}
            >
              {countries.map((country) => (
                <MenuItem key={country._id} value={country._id}>
                  {country.name}
                </MenuItem>
              ))}
            </Select>
          </FormControl>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose}>Cancel</Button>
          <Button onClick={handleSubmit} variant="contained">
            {editState ? "Update" : "Add"}
          </Button>
        </DialogActions>
      </Dialog>
    </Box>
  );
}

export default StatePage;
