import React, { useEffect, useState } from "react";
import {
  Box,
  Typography,
  Button,
  TextField,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  IconButton,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  MenuItem,
  Select,
  InputLabel,
  FormControl,
} from "@mui/material";
import { Edit, Delete } from "@mui/icons-material";
import api from "../utils/api";

function LocationPage() {
  const [locations, setLocations] = useState([]);
  const [countries, setCountries] = useState([]);
  const [states, setStates] = useState([]);
  const [cities, setCities] = useState([]);
  const [open, setOpen] = useState(false);
  const [editLocation, setEditLocation] = useState(null);
  const [form, setForm] = useState({
    name: "",
    address: "",
    country: "",
    state: "",
    city: "",
    postalCode: "",
  });

  const fetchLocations = async () => {
    const res = await api.get("/api/locations");
    setLocations(res.data);
  };
  const fetchCountries = async () => {
    const res = await api.get("/api/countries");
    setCountries(res.data);
  };
  const fetchStates = async (countryId) => {
    if (!countryId) return setStates([]);
    const res = await api.get(`/api/states/country/${countryId}`);
    setStates(res.data);
  };
  const fetchCities = async (stateId) => {
    if (!stateId) return setCities([]);
    const res = await api.get(`/api/cities/state/${stateId}`);
    setCities(res.data);
  };

  useEffect(() => {
    fetchLocations();
    fetchCountries();
  }, []);

  useEffect(() => {
    if (form.country) fetchStates(form.country);
    else setStates([]);
    setForm((f) => ({ ...f, state: "", city: "" }));
  }, [form.country]);

  useEffect(() => {
    if (form.state) fetchCities(form.state);
    else setCities([]);
    setForm((f) => ({ ...f, city: "" }));
  }, [form.state]);

  const handleOpen = (location = null) => {
    setEditLocation(location);
    setForm(
      location
        ? {
            name: location.name,
            address: location.address,
            country: location.country?._id || "",
            state: location.state?._id || "",
            city: location.city?._id || "",
            postalCode: location.postalCode || "",
          }
        : {
            name: "",
            address: "",
            country: "",
            state: "",
            city: "",
            postalCode: "",
          }
    );
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
    setEditLocation(null);
    setForm({
      name: "",
      address: "",
      country: "",
      state: "",
      city: "",
      postalCode: "",
    });
  };

  const handleSubmit = async () => {
    if (editLocation) {
      await api.put(`/api/locations/${editLocation._id}`, form);
    } else {
      await api.post("/api/locations", form);
    }
    fetchLocations();
    handleClose();
  };

  const handleDelete = async (id) => {
    await api.delete(`/api/locations/${id}`);
    fetchLocations();
  };

  return (
    <Box p={3}>
      <Typography variant="h6" mb={2}>
        Locations
      </Typography>
      <Button
        variant="contained"
        color="primary"
        onClick={() => handleOpen()}
        sx={{ mb: 2 }}
      >
        Add Location
      </Button>
      <TableContainer component={Paper}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell>Name</TableCell>
              <TableCell>Address</TableCell>
              <TableCell>Country</TableCell>
              <TableCell>State</TableCell>
              <TableCell>City</TableCell>
              <TableCell>Postal Code</TableCell>
              <TableCell>Actions</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {locations.map((location) => (
              <TableRow key={location._id}>
                <TableCell>{location.name}</TableCell>
                <TableCell>{location.address}</TableCell>
                <TableCell>{location.country?.name}</TableCell>
                <TableCell>{location.state?.name}</TableCell>
                <TableCell>{location.city?.name}</TableCell>
                <TableCell>{location.postalCode}</TableCell>
                <TableCell>
                  <IconButton onClick={() => handleOpen(location)}>
                    <Edit />
                  </IconButton>
                  <IconButton onClick={() => handleDelete(location._id)}>
                    <Delete />
                  </IconButton>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
      <Dialog open={open} onClose={handleClose}>
        <DialogTitle>
          {editLocation ? "Edit Location" : "Add Location"}
        </DialogTitle>
        <DialogContent>
          <TextField
            label="Name"
            value={form.name}
            onChange={(e) => setForm({ ...form, name: e.target.value })}
            fullWidth
            margin="normal"
          />
          <TextField
            label="Address"
            value={form.address}
            onChange={(e) => setForm({ ...form, address: e.target.value })}
            fullWidth
            margin="normal"
          />
          <FormControl fullWidth margin="normal">
            <InputLabel>Country</InputLabel>
            <Select
              value={form.country}
              label="Country"
              onChange={(e) => setForm({ ...form, country: e.target.value })}
            >
              {countries.map((country) => (
                <MenuItem key={country._id} value={country._id}>
                  {country.name}
                </MenuItem>
              ))}
            </Select>
          </FormControl>
          <FormControl fullWidth margin="normal">
            <InputLabel>State</InputLabel>
            <Select
              value={form.state}
              label="State"
              onChange={(e) => setForm({ ...form, state: e.target.value })}
              disabled={!form.country}
            >
              {states.map((state) => (
                <MenuItem key={state._id} value={state._id}>
                  {state.name}
                </MenuItem>
              ))}
            </Select>
          </FormControl>
          <FormControl fullWidth margin="normal">
            <InputLabel>City</InputLabel>
            <Select
              value={form.city}
              label="City"
              onChange={(e) => setForm({ ...form, city: e.target.value })}
              disabled={!form.state}
            >
              {cities.map((city) => (
                <MenuItem key={city._id} value={city._id}>
                  {city.name}
                </MenuItem>
              ))}
            </Select>
          </FormControl>
          <TextField
            label="Postal Code"
            value={form.postalCode}
            onChange={(e) => setForm({ ...form, postalCode: e.target.value })}
            fullWidth
            margin="normal"
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose}>Cancel</Button>
          <Button onClick={handleSubmit} variant="contained">
            {editLocation ? "Update" : "Add"}
          </Button>
        </DialogActions>
      </Dialog>
    </Box>
  );
}

export default LocationPage;
